import '../../styles/header.css';
import { useState } from 'react';

function Header(){
    const [isActive, setIsActive] = useState(false);
    return(
        <header>
            <div className="container">
                <div className="row dropdown">
                    <div className="header" onClick={(e)=>
                    setIsActive(!isActive)}>
                        <a className="logo-name">W</a>
                    </div>
                    {isActive && (
                        <div className='dropdown-content'>
                            <div className='dropdown-item'>Start</div>
                            <div className='dropdown-item'>Portifolio</div>
                            <div className='dropdown-item'>Sobre</div>
                            <div className='dropdown-item'>Contato</div>
                        </div>
                    )}
                </div>
            </div>
        </header>
    );
}

export default Header;