import '../styles/repository.css';
import { Box } from '@primer/react'
import { AnimateSharedLayout } from "framer-motion"


function Repository(){
    return(
        <div className="row repository">
            <h1 className='title-portifolio-repository'>Portifolio</h1>
            <Box display="flex">
                <Box p={3} flexGrow={20} borderColor="border.default">
                <div className='box-repository'>
                    <div className='projeto-1-repository'>
                        <h1 className='title-project-1'>Pet Virtual</h1>
                        <p className='description-project-1'>Sistema que simula uma Pet!</p>
                        <p className='language-project-1'>Linguagem: </p>
                        <a className='btn-acess-project-1' href='https://github.com/WilliamRodri/bichinho_virtual' target="_blank">Acesse o Repositorio</a>
                    </div>
                </div>
                </Box>
                <Box p={3} borderColor="border.default">
                <div className='box-repository'>
                    <div className='projeto-1-repository'>
                        <h1 className='title-project-1'>Matematicando</h1>
                        <p className='description-project-1'>Sistema com alguns sistemas matematico para ajudar!</p>
                        <p className='language-project-1'>Linguagem: </p>
                        <a className='btn-acess-project-1' href='https://github.com/WilliamRodri/matematicando_com_python' target="_blank">Acesse o Repositorio</a>
                    </div>
                </div>
                </Box>
                <Box p={3} borderColor="border.default">
                <div className='box-repository'>
                    <div className='projeto-1-repository'>
                        <h1 className='title-project-1'>Urna de Votao</h1>
                        <p className='description-project-1'>Sistema que simula uma urna de votao, bem completo!</p>
                        <p className='language-project-1'>Linguagem: </p>
                        <a className='btn-acess-project-1' href='https://github.com/WilliamRodri/urna_votacao' target="_blank">Acesse o Repositorio</a>
                    </div>
                </div>
                </Box>
            </Box>

            <div className='row'>
                <a href='https://github.com/WilliamRodri' target="_blank" className='ver-mais-repository'>Ver Repositorio completo </a>
            </div>

        </div>
    );
}

export default Repository;